.nav-menu {
  background: linear-gradient(180deg, #56CCF2 0%, rgba(47, 128, 237, 0) 100%), #2680C2;
  width: 260px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 34px;
  list-style: none;
  height: 69px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 10px 0px 0px 10px;
  transition: .5s;
}

.nav-text a:hover {
  background: #4389b8;
  /* color: #003E6B !important; */
}

.nav-text-active {
  background-color: white;
}

.auroraname {
  margin-left: 37px;
  margin-top: 4px;
  margin-bottom: 5px;
  list-style: none;
  font-size: 32px;
  line-height: 38px;
  color: #F0F4F8;
}

.clicked {
  color: #003E6B;
  background-color: #E5E5E5;
}

.clicked:hover {
  background: #E5E5E5 !important;
}

.clicked span {
  color: #003E6B;
}

.nav-menu-items {
  width: 100%;
}

span {
  margin-left: 16px;
